<template>
  <div class="hero is-info">
    <div class="hero-body is-illustrated fadeIn">
      <div class="columns">
        <div class="column is-hidden-mobile"></div>
        <div class="column is-8 has-text-centered">
          <div class="hero-title">
            <!--h1 class="title">
              AjedrezEV
            </h1-->
            <h2 class="subtitle">
              Navega, juega y aprende
            </h2>
            <h6></h6>
          </div>
          <div class="columns is-vcentered has-text-centered">
            <div class="column">
              <form id="search" class="has-text-centered" @submit.prevent="submit">
                <div class="field has-addons ">
                  <div class="control">
                    <input ref="input" class="input is-large is-rounded" name="query" type="text" placeholder="Evento, lugar, fecha, jugador o PGN" autofocus>
                  </div>
                  <div class="control">
                    <button type="submit" id="searchbtn" class="button is-large is-rounded is-info">
                      <span class="icon">
                        <span class="fas fa-search"></span>
                      </span>
                    </button>
                  </div>
                </div>
              </form>     
            </div>
          </div>       
          <div class="columns is-vcentered has-text-centered">
            <div class="column">
              <router-link class="button is-large is-white is-outlined" to="/stockfish">
                <span class="icon">
                  <span class="fas fa-chess-knight"></span>
                </span> 
                <span>Jugar</span>
              </router-link>              
            </div>
          </div>
        </div>
        <div class="column is-hidden-mobile"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'landing',
  mounted: function(){
    this.$nextTick(() => this.$refs.input.focus())
  },
  methods: {
    submit: function(){
      this.$router.push('/results?q=' + this.$refs.input.value)
    }
  },
  data () {
    return {
      msg: 'Welcome to JetsBooking'
    }
  }
}
</script>

<style type="text/css">
  
  #search input {
    box-shadow: 0 0 120px white;
  }

</style>



