<template>
  <div class="container">
    <div class="content column" v-if="!time.level">
      <div class="columns game-options has-text-centered is-vcentered">
        <div class="column">
          <h4>Jugar con</h4>
          <div class="control has-text-centered column">
            <div class="buttons levels has-addons">
              <button class="button is-large has-background-black" @click="setPlayerColor('white')" :class="{'is-selected' : playerColor==='white'}">
                <span class="icon has-text-white">
                  <span class="fas fa-chess-knight"></span>
                </span>
              </button>
              <button class="button is-large" @click="setPlayerColor('random')" :class="{'is-selected' : playerColor==='random'}">
                <span class="icon has-text-grey">
                  <span class="fas fa-chess-board"></span>
                </span>
              </button>
              <button class="button is-large has-background-light" @click="setPlayerColor('black')" :class="{'is-selected' : playerColor==='black'}">
                <span class="icon has-text-black">
                  <span class="fas fa-chess-knight"></span>
                </span>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="columns has-text-centered">
        <div class="column">
          <h4>Nivel</h4>
          <div class="control has-text-centered column">
            <div class="buttons levels has-addons">
              <button class="button" @click="gameStart(1)">
                <span>1</span>
              </button>
              <button class="button" @click="gameStart(4)">
                <span>2</span>
              </button>
              <button class="button" @click="gameStart(6)">
                <span>3</span>
              </button>
              <button class="button" @click="gameStart(8)">
                <span>4</span>
              </button>
              <button class="button" @click="gameStart(10)">
                <span>5</span>
              </button>
              <button class="button" @click="gameStart(12)">
                <span>6</span>
              </button>
              <button class="button" @click="gameStart(14)">
                <span>7</span>
              </button>
              <button class="button" @click="gameStart(16)">
                <span>8</span>
              </button>
              <button class="button" @click="gameStart(18)">
                <span>9</span>
              </button>
              <button class="button" @click="gameStart(20)">
                <span>10</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="board-container" v-else>
      <div class="columns">
        <div class="column">
          <div id="board"></div>
        </div>
        <div class="column datospartida">
          <div class="columns acciones">
            <div class="column">
              <span v-html="ecode" class=""></span>&nbsp;
              <span v-html="opening" class="has-text-black"></span>
            </div>
            <div class="column has-text-centered"  v-if="pgnIndex.length > 1">
              <button @click="gameRestart()" class="button is-danger">
                <span class="icon has-text-white">
                  <span class="fas fa-flag"></span>
                </span>
              </button>
            </div>
            <div class="column has-text-centered" v-if="announced_game_over">
              <button @click="gameRestart()" class="button is-success">
                <span>REMATCH</span>
              </button>
            </div>
          </div>  
          <div class="columns gamepgn is-hidden-mobile">
            <div class="movesTableContainer">
              <div class="movesTable">
                <div class="moveRow" v-for="(move,index) in pgnIndex">
                  <div class="moveNumCell" :class="{ 'moveRowOdd': move.odd, 'moveRowEven': !move.odd }">
                    <span v-html="(index+1)"></span>
                  </div>

                  <div class="moveCell moveSAN movew" :class="{ 'moveRowOdd': move.odd, 'moveRowEven': !move.odd }">
                    <a class="moveindex" :href="'#'+(move.i-2)">
                      <span v-html="move.white"></span>
                    </a>
                  </div>

                  <div class="moveCell moveSAN moveb" :class="{ 'moveRowOdd': move.odd, 'moveRowEven': !move.odd }">
                    <a class="moveindex" :href="'#'+(move.i-1)">
                      <span v-html="move.black"></span>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  import axios from 'axios'
  import Chess from 'chess.js'
  import Chessboard from '../../assets/js/chessboard'
  import snackbar from '../components/Snackbar'

  export default {
    name: 'stockfish',
    mounted: function(){
      //this.$root.loading = true
      window.app = this

      if (!Worker || (location && location.protocol === "file:")) {
        var script_tag  = document.createElement("script");
        script_tag.type ="text/javascript";
        script_tag.src  = "/assets/js/stockfish.js";
        script_tag.onload = init;
        document.getElementsByTagName("head")[0].appendChild(script_tag);
      }

      var player = localStorage.getItem('player')
      axios.get('/assets/json/eco_es.json')
        .then((response)=>{
        this.eco = response.data
      })

      this.$root.player = player
    },
    methods: {
      gameRestart: function() {
        var t = this
        t.game.reset()
        t.announced_game_over = false
        t.pgnIndex = []
        t.ecode = ''
        t.opening = ''
        t.uciCmd('setoption name Contempt value 0')
        t.setSkillLevel(0)
        t.uciCmd('setoption name King Safety value 0')
      },
      gamePGN:function(pgn){
        var data = []
        pgn.split('.').forEach(function(turn){
          turn.split(' ').forEach(function(move){
            if(move.length){
              if(isNaN(move) && move.length > 1){
                data.push(move)
              }
            }
          })
        })
        return data
      },
      uciCmd: function(cmd, which) {
        //console.log("UCI: " + cmd);
        (which || this.engine).postMessage(cmd);
      },    
      gameStart: function(level){

        var t = this

        t.engine = typeof STOCKFISH === "function" ? STOCKFISH() : new Worker('/assets/js/stockfish.js');
        t.evaler = typeof STOCKFISH === "function" ? STOCKFISH() : new Worker('/assets/js/stockfish.js');
        t.engineStatus = {};
        t.uciCmd('uci')
        t.setSkillLevel(level)

        t.evaler.onmessage = function(event) {
            var line;
            
            if (event && typeof event === "object") {
                line = event.data;
            } else {
                line = event;
            }
            
            //console.log("evaler: " + line);
            
            /// Ignore some output.
            if (line === "uciok" || line === "readyok" || line.substr(0, 11) === "option name") {
                return;
            }
            
            //if (evaluation_el.textContent) {
                //evaluation_el.textContent += "\n";
            //}
            //evaluation_el.textContent += line;
        }

        t.engine.onmessage = function(event) {
          var line;
          var t = window.app

          if (event && typeof event === "object") {
            line = event.data;
          } else {
            line = event;
          }
          //console.log("Reply: " + line)
          if(line == 'uciok') {
            t.engineStatus.engineLoaded = true;
          } else if(line == 'readyok') {
            t.engineStatus.engineReady = true;
          } else {
            var match = line.match(/^bestmove ([a-h][1-8])([a-h][1-8])([qrbn])?/);
            /// Did the AI move?
            if(match) {
              t.isEngineRunning = false;
              const move = t.game.move({from: match[1], to: match[2], promotion: match[3]});
              t.prepareMove();
              t.uciCmd("eval", t.evaler)
              //evaluation_el.textContent = "";
              t.uciCmd("eval");

              if(move) {
                t.updateMoves(move)
              }
            /// Is it sending feedback?
            } else if(match = line.match(/^info .*\bdepth (\d+) .*\bnps (\d+)/)) {
              t.engineStatus.search = 'Depth: ' + match[1] + ' Nps: ' + match[2];
            }
            
            /// Is it sending feed back with a score?
            if(match = line.match(/^info .*\bscore (\w+) (-?\d+)/)) {
              var score = parseInt(match[2]) * (t.game.turn() == 'w' ? 1 : -1);
              /// Is it measuring in centipawns?
              if(match[1] == 'cp') {
                t.engineStatus.score = (score / 100.0).toFixed(2);
              /// Did it find a mate?
              } else if(match[1] == 'mate') {
                t.engineStatus.score = 'Mate in ' + Math.abs(score);
              }
              
              /// Is the score bounded?
              if(match = line.match(/\b(upper|lower)bound\b/)) {
                t.engineStatus.score = ((match[1] == 'upper') == (t.game.turn() == 'w') ? '<= ' : '>= ') + t.engineStatus.score
              }
            }
          }

          if(t.engineStatus.engineLoaded && t.playerColor==='black'){
            setTimeout(() => {
              t.prepareMove();  
            },1000)
          }
          t.displayStatus();
        }

        setTimeout(() => {
          this.boardEl = document.getElementById('board')
          this.game = new Chess()
          if(window.innerWidth < 789){
            this.boardCfg.draggable = false 
          }
          this.board = Chessboard('board', this.boardCfg)
          this.board.orientation(this.playerColor)

          // resize event handling
          $(window).resize(() => {
              this.board.resize()
          })

          this.$root.loading = false
          this.boardTaps()
        },500)
      },   
      boardTaps:function(){
        $('.square-55d63').on('mousedown touchstart',(e) => {
          var t = window.app
          const src = $(e.target).attr('src')
          const target = $(e.target).attr('src') ? $(e.target).parent() : $(e.target)
          const square = target.attr('id').substring(0,2)

          if(!t.moveFrom){
            if(!src){ // blank square
              t.removeHighlight()
              return
            } 
            if($(e.target).is('img')){
              $(e.target).parent().addClass('highlight-move')
            } else {
              $(e.target).addClass('highlight-move')
            }
            t.moveFrom = square
          } else {

            if(square === t.moveFrom) return

            var moveObj = ({
              from: t.moveFrom,
              to: square,
              promotion: 'q' // NOTE: always promote to a queen for example simplicity
            });

            t.moveFrom = null
            var move = t.game.move(moveObj)

            // illegal move
            if (move === null) {
              t.removeHighlight()
              return 'snapback'
            }

            t.board.position(t.game.fen())
            t.updateMoves(move)
            setTimeout(() => {
              t.prepareMove();  
            },1000)    
          }
        })
      },
      playSound: function(audio,vol){
        if(vol===undefined) vol = 1
        if(audio===undefined) audio = "move";
        var audio = new Audio('/assets/audio/' + audio + '.ogg')
        audio.vol = vol
        audio.play()
      },
      get_moves: function()
      {
        var moves = '';
        var pgn = []
        var history = this.game.history({verbose: true});
        
        for(var i = 0; i < history.length; ++i) {
            var move = history[i];
            moves += ' ' + move.from + move.to + (move.promotion ? move.promotion : '');
        }

        return moves;
      },
      prepareMove : function() {
        var t = this
        //stopClock();
        //$('#pgn').text(game.pgn());
        //updateClock();
        var turn = t.game.turn() == 'w' ? 'white' : 'black';
        t.board.position(t.game.fen());        

        if(!t.game.game_over()) {
          if(turn != t.playerColor) {
            t.uciCmd('position startpos moves' + t.get_moves());
            t.uciCmd('position startpos moves' + t.get_moves(), t.evaler);
            //evaluation_el.textContent = "";
            t.uciCmd("eval", t.evaler);
            
            if (t.time && t.time.wtime) {
                t.uciCmd("go " + (t.time.depth ? "depth " + t.time.depth : "") + " wtime " + t.time.wtime + " winc " + t.time.winc + " btime " + t.time.btime + " binc " + t.time.binc);
            } else {
                t.uciCmd("go " + (t.time.depth ? "depth " + t.time.depth : ""));
            }
            t.isEngineRunning = true;
          }
          /*
          if(t.game.history().length >= 2 && !time.depth && !time.nodes) {
              startClock();
          }*/
        }
      },   
      removeHighlight: function(){
        document.querySelectorAll(this.squareClass).forEach((item) => {
          item.classList.remove('highlight-move');
        })
      },
      updateMoves:function(move){

        var t = this

        this.playSound()

        t.removeHighlight()

        document.querySelector('.square-' + move.from).classList.add('highlight-move')
        document.querySelector('.square-' + move.to).classList.add('highlight-move')

        const game_pgn = t.game.pgn()
        t.pgnIndex = this.gamePGNIndex(game_pgn)

        const pgn = game_pgn.split('.').join('')

        t.eco.forEach((eco,i) => {
          if(eco.pgn === pgn){
            t.opening = eco.name
            t.ecode = eco.eco
          }
        })
        
        if(t.game.game_over()){
          var turn = t.game.turn() == 'w' ? 'negras' : 'blancas';
          snackbar('success','Ganan ' + turn)
          t.announced_game_over = true
        }
      },
      gamePGNIndex:function(pgn){
        var data = []
        , index = 0
        , selectedIndex = parseInt(location.hash.replace('#',''))
        , symbols = [
          {K:'',Q:'',B:'',N:'',R:'',p:''},
          {K:'',Q:'',B:'',N:'',R:'',p:''}
        ]
        pgn.split('.').forEach(function(turn,i){
          const white = turn.split(' ')[1]||''
          const black = turn.split(' ')[2]||''
          if(isNaN(white)){
            data.push({
              white:white,
              black:black,
              i:Math.ceil(i*2),
              odd:i%2==0
            })
          }
        })
        return data
      },  
      setPlayerColor:function(color){
        const allow = ['white','black']
        if(!allow[color]){
          color = allow[Math.floor(Math.random() * allow.length)]
        }
        this.playerColor = color
      },
      setSkillLevel: function(skill) {
        var max_err,
            err_prob,
            difficulty_slider;
          var t = this
        
        if (skill < 0) {
            skill = 0;
        }
        if (skill > 20) {
            skill = 20;
        }
        
        t.time.level = skill;
        
        /// Change thinking depth allowance.
        if (skill < 5) {
          t.time.depth = "1";
        } else if (skill < 10) {
          t.time.depth = "2";
        } else if (skill < 15) {
          t.time.depth = "3";
        } else {
          /// Let the engine decide.
          t.time.depth = "";
        }
        
        t.uciCmd('setoption name Skill Level value ' + skill);
        ///NOTE: Stockfish level 20 does not make errors (intentially), so these numbers have no effect on level 20.
        /// Level 0 starts at 1
        err_prob = Math.round((skill * 6.35) + 1);
        /// Level 0 starts at 10
        max_err = Math.round((skill * -0.5) + 10);
        
        t.uciCmd('setoption name Skill Level Maximum Error value ' + max_err);
        t.uciCmd('setoption name Skill Level Probability value ' + err_prob);
      },
      displayStatus: function() {
        var t = this
        var status = 'Motor: ';
        if(!t.engineStatus.engineLoaded) {
          status += 'Cargando...';
        } else if(!t.engineStatus.engineReady) {
          status += 'Cargado';
        } else {
          status += 'Listo';
        }

        if(t.engineStatus.search) {
          status += '<br>' + t.engineStatus.search;
          if(t.engineStatus.score && t.displayScore) {
            status += (t.engineStatus.score.substr(0, 4) === "Mate" ? " " : ' Score: ') + t.engineStatus.score;
          }
        }

        t.status = status
        //$('#engineStatus').html(status);
      },
      onDragStart : function(source, piece, position, orientation) {
        var re = this.playerColor == 'white' ? /^b/ : /^w/
        if (this.game.game_over() ||
          piece.search(re) !== -1) {
          return false;
        }
      },
      onDrop : function(source, target) {
        // see if the move is legal
        var move = this.game.move({
          from: source,
          to: target,
          promotion: 'q'
        });

        // illegal move
        if (move === null) return 'snapback';

        this.updateMoves(move)
        setTimeout(() => {
          this.prepareMove();  
        },1000)        
      },
      onSnapEnd: function() {
        this.board.position(this.game.fen(),false);
      },
      gameFlip: function(){
        this.board.flip()
        const head = document.querySelector('.b-area > .head').innerHTML
        const foot = document.querySelector('.b-area > .foot').innerHTML
        document.querySelector('.b-area > .head').innerHTML = foot
        document.querySelector('.b-area > .foot').innerHTML = head
      }
    },
    data () {
      return {
        boardCfg: {
          showErrors:true,
          position: 'start',
          draggable: true,
          onDragStart: this.onDragStart,
          onDrop: this.onDrop,
          onSnapEnd: this.onSnapEnd,
          pieceTheme:'/assets/img/chesspieces/classic/{piece}.png'
        },
        time: {
          level: 0
        },
        displayScore: true,
        status:'',
        moveFrom:null,
        isEngineRunning: false,
        announced_game_over:false,
        playerColor:'white',
        data:{},
        eco:{},
        ecode:'&nbsp;',
        opening:'&nbsp;',
        board:null,
        game:null,
        pgnIndex:[],
        gameMoves:[],
        squareClass:'.square-55d63'    
      }
    }
  }
</script>


<style type="text/css">
  .game-options {
    padding-top: 0.75rem;
  }
  .is-selected {
    border: 2px solid #1f9cef;
  }
  .levels {
    justify-content: center;
  }
  .highlight-move {
    filter: saturate(400%);
  }
  .is-pgn {
    line-height: 2rem;
    font-size: 0.75rem;
  }
  .moveCell.active {
    animation:.25s ease 0s normal forwards 1 contrastIn;
    -webkit-animation:.25s ease 0s normal forwards 1 contrastIn;
    background-color:#23d160;
    border-color: transparent;
    color: white;
  }
  .moveCell.active a {
    background-color:#23d160;
    color: white;
  }
  .status {
    display: inline-block;
    background-color: white;
    padding: 0.25rem;
    width: 100%;
    position: fixed;  
    z-index: 10;
    bottom: 0;
  }
  .b-area {
    max-height:0;
    transition: all 1s linear;
  }
  .head, .foot {
    display: none;
  }
  .b-area.show {
    height:auto;
    max-height:600px;     
    opacity: 1;
  }
  .b-area h6 {
    margin-bottom: 0;
    padding: 0.75rem;
    margin-right: 4px;
    min-height: 42px;
    clear: both;
  }
  .datospartida {
    padding: 0;
  }
  .acciones {
    padding: 0;
    padding-left: 1rem;
  }

  @media screen and (min-width: 767px), print {
    .acciones {
      margin-top: 0.75rem;
    }
  }
</style>



