<template>
  <div v-show="!$root.loading">
    <div class="i-area-container">
      <div class="i-area content">
        <div class="columns is-result is-vcentered has-text-centered">
          <div class="column has-background-white">
            <h1><span></span> Aperturas</h1>
          </div>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="content column">
        <div class="columns">
          <div class="column">
            <div class="b-area">
              <div class="head" @click="gameFlip">
                <h6 class="has-text-left has-background-black">
                  <span v-show="data.result==='0-1'"></span>
                  <span class="has-text-white" v-html="data.black"></span> 
                  <span class="has-text-grey" v-html="data.blackelo"></span>
                </h6>
              </div>
              <div id="board" @click="gamePause"></div>
              <div class="foot" @click="gameFlip">
                <h6 class="has-text-right has-background-white">
                  <span v-show="data.result==='1-0'"></span>
                  <span class="has-text-black" v-html="data.white"></span> 
                  <span class="has-text-grey" v-html="data.whiteelo"></span>
                </h6>
              </div>
            </div>
          </div>
          <div class="column datospartida">
            <!--h5 class="has-text-black"> Datos de la partida</h5-->
            <table class="table shrink-th" v-if="Object.keys(data).length">
              <tr>
                <th width="1%"></th>
                <th></th>
              </tr>
              <tr class="has-background-success">
                <td class="has-text-weight-bold">
                  <span class="has-text-white">Apertura</span>
                </td>
                <td>
                  <span v-html="opening" class="has-text-white">...</span>
                </td>
              </tr>
              <tr v-show="data.pgn">
                <td colspan=2>
                  <span class="is-pgn" v-html="$root.pgnIndex(data)"></span>
                </td>
              </tr>  
              <tr v-show="data.event && data.event !='?'">
                <td class="has-text-weight-bold">
                  <span>Evento</span>
                </td>
                <td>
                  <span v-html="data.event"></span>
                </td>
              </tr>
              <tr v-show="data.round && data.round!='?'">
                <td class="has-text-weight-bold">
                  <span>Ronda</span>
                </td>
                <td>
                  <span v-html="data.round"></span>
                </td>
              </tr>
              <tr v-show="data.date && data.date !='?'">
                <td class="has-text-weight-bold">
                  <span>Fecha</span>
                </td>
                <td>
                  <span v-html="data.date"></span>
                </td>
              </tr>              
              <!--tr v-show="data.white">
                <td class="has-text-weight-bold">
                  <span>Blancas</span>
                </td>
                <td>
                  <span v-html="data.white"></span> <span v-html="data.whiteelo"></span>
                </td>
              </tr>
              <tr v-show="data.black">
                <td class="has-text-weight-bold">
                  <span>Negras</span>
                </td>
                <td>
                  <span v-html="data.black"></span> <span v-html="data.blackelo"></span>
                </td>
              </tr-->
              <tr v-show="data.site && data.site !='?'">
                <td class="has-text-weight-bold">
                  <span>Lugar</span>
                </td>
                <td>
                  <span v-html="data.site"></span>
                </td>
              </tr>
              <!--tr v-show="data.result">
                <td class="has-text-weight-bold">
                  <span>Resultado</span>
                </td>
                <td>
                  <span v-html="data.result"></span>
                </td>
              </tr-->
            </table>

            <!-- h5 class="has-text-black"> Atajos de teclado</h5>
            <div class="notification is-info">
              <table class="keyboard-tips">
                <tr>
                  <td>[]</td>
                  <td>Navega movimientos de la partida</td>
                </tr>
                <tr>
                  <td>[]</td>
                  <td><p>Establece velocidad de la animacin</p></td>
                </tr>
                <tr>
                  <td>[SPACE]</td>
                  <td><p>Pausa/Reanuda la partida</p></td>
                </tr>
                <tr>
                  <td>[SHIFT] + [F]</td>
                  <td><p>Invierte el tablero</p></td>
                </tr>
                <tr>
                  <td>[SHIFT] + [N]</td>
                  <td><p>Visin nocturna</p></td>
                </tr>
              </table>
            </div-->

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  window.onscroll = function () {
  "use strict";
    let bar = document.querySelector('.bar-progress')
    if($('body').scrollTop() > 10) {
      if (!bar.classList.contains('paused')) {
        bar.classList.toggle('paused')
      }
    } else {
      if (bar.classList.contains('paused')) {
        bar.classList.toggle('paused')
      }
    }
  }

</script>

<script>

  import axios from 'axios'
  import Chess from 'chess.js'
  import Chessboard from 'chessboardjs'
  import snackbar from '../components/Snackbar'
  import swal from 'sweetalert'

  export default {
    name: 'eco',
    watch: {
      '$route': function () {
        this.gameSeek()
      }
    },
    mounted: function(){
      if(localStorage.getItem('speed')){
        this.speed = parseInt(localStorage.getItem('speed'))
      }

      axios.get('/assets/json/eco.json')
        .then((response)=>{
        this.eco = response.data
        this.gameStart()
      })
    },
    methods: {
      gameMove:function(){
        if(!this.paused){
          var move = this.gameMoves[this.index];
          this.selectedIndex = parseInt(location.hash.replace('#',''))

          // exit if the game is over
          if (!move || this.game.game_over() === true ||
            this.game.in_draw() === true ||
            this.gameMoves.length === 0) return;

          this.getColorHightlight(move)

          document.querySelectorAll(this.squareClass).forEach((item) => {
            item.classList.remove('highlight-' + this.colorToHighlight);
          })

          document.querySelectorAll('.moveindex').forEach((item) => {
            item.classList.remove('active');
          })

          document.querySelector('.moveindex[href="#' + this.index + '"]').classList.add('active');

          var perc = (this.index + 1) / this.gameMoves.length * 100;
          $('.bar-progress').animate({width:perc+'%'},this.speed,'linear')
          this.index++
          this.game.move(move)
          var pgn = []

          this.game.history().forEach((move,i) => {
            if(i%2){
              pgn.push(move)
            } else {
              pgn.push([Math.ceil(i/2)+1,move].join(' '))     
            }   
          })

          const pgnt = pgn.join(' ')
          this.eco.forEach((eco,i) => {
            if(eco.pgn === pgnt){
              this.opening = eco.name
            }
          })

          this.board.position(this.game.fen());  

          if(this.index === this.gameMoves.length){
            this.gamePause()
          }

          setTimeout(this.gameMove, this.speed)
        }
      },
      getColorHightlight: function(move) {
        var square = move.replace(/[^\w\s]/gi,'')
        square = square.substr(-2)
        this.squareToHighlight = square;

        if (this.index%2===0) {
          this.colorToHighlight = 'white';
        }
        else {
          this.colorToHighlight = 'black';    
        }
      },
      gamePGN:function(pgn){
        var data = []
        pgn.split('.').forEach(function(turn){
          turn.split(' ').forEach(function(move){
            if(move.length){
              if(isNaN(move) && move.length > 1){
                data.push(move)
              }
            }
          })
        })
        return data
      },
      gameStart: function(){
        this.$root.loading = true
        axios.post( this.$root.endpoint + '/game', {room:this.$route.params.game} ).then((res) => {
          if(!Object.keys(res.data).length) return location.href="/404"
          var game = res.data
          const totalms = this.$root.countMoves(game.pgn) * this.speed
          this.gameMoves = this.gamePGN(game.pgn)
          this.data = game
          this.duration = totalms / 1000
          this.$root.loading = false

          /*
          setTimeout(() => {
            axios.post( this.$root.endpoint + '/playlist', {white:game.white,black:game.black} ).then((res) => {
              console.log("playlist")
              console.log(res.data)
            })            
          },5000)*/

            this.boardEl = document.getElementById('board')
            this.game = new Chess()
            this.board = Chessboard('board', this.boardCfg)      
            document.querySelector('.i-area-container').classList.add('slideOut')

          setTimeout(() => {
            
            setTimeout(() => {
              document.querySelector('.b-area').classList.add('show')
              setTimeout(() => {
                /* autoplay kickstart */
                this.gameSeek()
              }, 500)
            }, 500)
          },3000)
        })
      },
      gameFlip: function(){
        this.board.flip()
        const head = document.querySelector('.b-area > .head').innerHTML
        const foot = document.querySelector('.b-area > .foot').innerHTML
        document.querySelector('.b-area > .head').innerHTML = foot
        document.querySelector('.b-area > .foot').innerHTML = head
      },
      gameSeek:function(){
        window.setTimeout(() => {
          this.selectedIndex = parseInt(location.hash.replace('#',''))
          if(!isNaN(this.selectedIndex)) {
            this.gamePos(this.selectedIndex)
          }
          this.gameMove()
          if(!isNaN(this.selectedIndex) && !this.paused) {
            this.gamePause()
          }
        }, 500)
      },
      gamePos:function(pos){
        if(pos > this.gameMoves.length){
          return
        }

        this.index = pos

        const moves = this.gameMoves.slice(0,this.index+1)
        var move = this.gameMoves[this.index];
        this.getColorHightlight(move)        
        var pgn = []
        moves.forEach((move,i) => {
          if(i%2){
            pgn.push(move)
          } else {
            pgn.push([Math.ceil(i/2)+1,move].join('. '))     
          }   
        })

        document.querySelectorAll(this.squareClass).forEach((item) => {
          item.classList.remove('highlight-white')
          item.classList.remove('highlight-black')
        })

        document.querySelectorAll('.moveindex').forEach((item) => {
          item.classList.remove('active');
        })

        document.querySelector('.moveindex[href="#' + this.index + '"]').classList.add('active');
        var perc = (this.index + 1) / this.gameMoves.length * 100;
        $('.bar-progress').animate({width:perc+'%'},100,'linear')
        const pgns = pgn.join(' ')
        this.game.reset()
        this.game.load_pgn(pgns) 
        this.board.position(this.game.fen())
      },
      gamePause:function(){
        this.paused = !this.paused
        document.querySelector('.bar-progress').classList.remove('paused')
        if(this.paused){
          document.querySelector('.bar-progress').classList.add('paused')
        } else {
          setTimeout(this.gameMove, 500)
        }
      },
      gameSpeed:function(s){
        this.speed+= s
        if(this.speed >= 1000 && this.speed <= 10000){
          localStorage.setItem('speed',speed)
        } else {
          this.speed-=s
        }
      },
      onMoveEnd: function() {
        document.querySelectorAll('.square-' + this.squareToHighlight).forEach((square) => {
          square.classList.add('highlight-' + this.colorToHighlight);
          })
      },
      setClock : function(){
        this.gamePause()
        swal("Ingresa el intervalo en milisegundos entre 1000/60000", {
          content: {
            element: 'input',
            attributes: {
              placeholder: "Valor en milisegundos",
              value: this.speed
            }
          },
          closeOnClickOutside:false
        })
        .then((speed) => {
          if(speed){
            speed = parseInt(speed)
            if(speed > 60000 || speed < 1000){
              swal('El valor debe ser entre 1000 / 60000')
            } else {
              this.speed = speed
              localStorage.setItem('speed',speed)
              const totalms = this.$root.countMoves(this.data.pgn) * this.speed
              this.duration = totalms / 1000
            }          
          }
          this.gamePause()              
        })
        .catch(() => {
          this.gamePause()
        });        
      }
    },
    data () {
      return {
        boardCfg: {
          showErrors:true,
          position: 'start',
          draggable: false,
          onMoveEnd: this.onMoveEnd,
          moveSpeed:350,
          pieceTheme:'/assets/img/chesspieces/wikipedia/{piece}.png'
        },
        data:{},
        eco:{},
        duration:0,
        opening:null,
        board:null,
        game:null,
        gameMoves:[],
        room: location.pathname.replace('/',''),
        selectedIndex: parseInt(location.hash.replace('#','')),
        boardEl:document.getElementById('board'),
        index:0,
        paused:false,
        speed:3000,
        squareToHighlight:null,
        colorToHighlight:null,
        squareClass:'.square-55d63'
      }
    }
  }
</script>

<style type="text/css">
  .datospartida {
    margin-top: 0.5rem;
  }
  .highlight-white {
    -webkit-box-shadow: inset 0 0 1px 6px yellow;
    -moz-box-shadow: inset 0 0 1px 6px yellow;
    box-shadow: inset 0 0 1px 6px yellow;  
  }
  .highlight-black {
    -webkit-box-shadow: inset 0 0 1px 6px blue;
    -moz-box-shadow: inset 0 0 1px 6px blue;
    box-shadow: inset 0 0 1px 6px blue;  
  }
  .is-pgn {
    line-height: 2rem;
    font-size: 0.75rem;
  }
  .moveindex {
    font-size: 0.5rem;
    padding: 0 0.25rem;
    margin: 0;
    border-radius: 35px;
    border: 1px solid #FA7C91;
    font-size: 1rem;
  }
  .moveindex.active {
    background-color:#23d160;
    border-color: transparent;
    color: white;
  }
  .status {
    display: inline-block;
    background-color: #fff;
    padding: 0.25rem;
    width: 100%;
    position: fixed;  
    z-index: 10;
    bottom: 0;
  }
  .i-area-container {
    display: inline-block;
    background-color: black;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 199;
  }
  .i-area > .columns > .column{
    height: 50vh;
  }
  .i-area .is-result .column {
    padding-top: 5rem;
  }
  .i-area h1 {
    font-size: 5rem;
    min-height: 5rem;
  }
  .b-area {
    opacity: 0;
    max-height:0;
    padding-top: 0.5rem;
    position: relative;
  }
  .b-area.show {
    height:auto;
    max-height:600px;     
    opacity: 1;
  }
  .b-area h6 {
    margin-bottom: 0;
    padding: 0.75rem;
    line-height: 0.25rem;
    margin-right: 4px;
    min-height: 30px;
    clear: both;
  }
  .b-area .foot h6 {
    -webkit-border-top-left-radius: 8px;
    -webkit-border-bottom-left-radius: 8px;
    -moz-border-radius-topleft: 8px;
    -moz-order-radius-bottomleft: 8px;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
  }
  .b-area .head h6 {
    -webkit-border-top-right-radius: 8px;
    -webkit-border-bottom-right-radius: 8px;
    -moz-border-radius-topright: 8px;
    -moz-border-radius-bottomright: 8px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
  }
  .b-area .head {
    position: absolute;
    z-index: 1;
    left: -0.5rem;
    top: -0.75rem;
  }
  .b-area .foot {
    position: absolute;
    z-index: 1;
    bottom: -1.5rem;
    right: -0.5rem;
  }
  .bar {
    position: relative;
    z-index: 99;
    background-color: rgba(0,0,0,0.1);
    border-radius: 5px;
    cursor: pointer;
  }
  .bar-progress {
    height: 0.5rem;
    background-color: red;
    border-radius: 5px;
    width: 0;
  }  
  .bar-progress.paused {
    background-color: grey;
  }  

  .pietimer {
    position: relative;
    margin: 40px auto;
    background: white;
  }

  .pietimer, .pietimer * {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }

  .pietimer {
    width: 250px;
    height: 250px;
  }

  .pietimer .pie {
    width: 50%;
    height: 100%;
    transform-origin: 100% 50%;
    position: absolute;
    background: #08C;
    border: 5px solid rgba(0,0,0,0.5);
  }

  .pietimer .spinner {
    border-radius: 100% 0 0 100% / 50% 0 0 50%;
    z-index: 200;
    border-right: none;
    animation: rota 5s linear infinite;
  }

  .pietimer:hover .spinner,
  .pietimer:hover .filler,
  .pietimer:hover .mask {
    animation-play-state: running;
  }

  .pietimer .filler {
    border-radius: 0 100% 100% 0 / 0 50% 50% 0;
    left: 50%;
    opacity: 0;
    z-index: 100;
    animation: opa 5s steps(1, end) infinite reverse;
    border-left: none;
  }

  .pietimer .mask {
    width: 50%;
    height: 100%;
    position: absolute;
    background: inherit;
    opacity: 1;
    z-index: 300;
    animation: opa 5s steps(1, end) infinite;
  }

  @keyframes rota {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes opa {
    0% {
      opacity: 1;
    }
    50%, 100% {
      opacity: 0;
    }
  }  
</style>


