<template>
  <div class="game-container" v-show="!$root.loading">
    <!--div class="playlist-container">
      <div class="pietimer">
        <div class="pie spinner"></div>
        <div class="pie filler"></div>
        <div class="mask"></div>
      </div>
    </div-->
    <div class="pres-container">
      <div class="pres content">
        <div class="columns is-result is-vcentered has-text-centered">
          <div class="column is-player-white">
            <h1><span v-show="data.result==='1-0'"></span></h1>
            <h3 class="has-text-black" v-html="data.white"></h3>
          </div>
          <div class="column is-player-black">
            <h1><span v-show="data.result==='0-1'"></span></h1>
            <h3 class="has-text-white" v-html="data.black"></h3>
          </div>
        </div>
        <div class="columns is-vcentered has-text-centered is-player-info">
          <div class="column">
            <h6 v-show="data.date && data.date !='?'" v-html="data.date"></h6>
            <h6 v-show="data.event && data.event !='?'" v-html="data.event"></h6>
            <h6 v-show="data.site && data.site !='?'" v-html="data.site"></h6>
          </div>
        </div>
      </div>
    </div>
    <div class="status">
      <span class="has-text-weight-semibold">
        <a href="#" @click="setClock">
          &nbsp; <!-- <span v-html="speed / 1000"></span>s--> <span v-html="$root.msToTime(duration)"></span>
        </a>
      </span>
      <div class="bar">
        <div class="bar-progress"></div>
      </div>
    </div>  
    <div class="container">
      <div class="content column">
        <div class="columns">
          <div class="column">
            <div class="board">
              <h6 class="has-text-left" @click="gameFlip">
                <span v-show="data.result==='0-1'"></span>
                <span v-html="data.black"></span> 
                <span class="has-text-grey" v-html="data.blackelo"></span>
              </h6>
              <div id="board" @click="gamePause"></div>
              <h6 class="has-text-right" @click="gameFlip">
                <span v-show="data.result==='1-0'"></span>
                <span v-html="data.white"></span> 
                <span class="has-text-grey" v-html="data.whiteelo"></span>                  
              </h6>
            </div>
          </div>
          <div class="column datospartida">
            <!--h5 class="has-text-black"> Datos de la partida</h5-->
            <div v-if="Object.keys(data).length">
              <div class="columns">
                <div class="column">
                  <span v-html="ecode" class=""></span>&nbsp;
                  <span v-html="opening" class="has-text-black"></span>
                </div>
              </div>  
              <div class="columns gamepgn">
                <div class="movesTableContainer">
                  <div class="movesTable">
                    <div class="moveRow" v-for="(move,index) in pgnIndex">
                      <div class="moveNumCell" :class="{ 'moveRowOdd': move.odd, 'moveRowEven': !move.odd }">
                        <span v-html="(index+1)"></span>
                      </div>

                      <div class="moveCell moveSAN movew" :class="{ 'moveRowOdd': move.odd, 'moveRowEven': !move.odd }">
                        <a class="moveindex" :href="'#'+(move.i-2)">
                          <span v-html="move.white"></span>
                        </a>
                      </div>

                      <div class="moveCell moveSAN moveb" :class="{ 'moveRowOdd': move.odd, 'moveRowEven': !move.odd }">
                        <a class="moveindex" :href="'#'+(move.i-1)">
                          <span v-html="move.black"></span>
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="columns">
                <div class="column" v-show="data.date && data.date !='?'">
                  <span class=""  v-html="data.date"></span>
                </div>
                <div class="column" v-show="data.event && data.event !='?'">
                  <span class="" v-html="data.event"></span>
                </div>
                <div class="column" v-show="data.round && data.round !='?'">
                  <span class="" v-html="data.round"></span>
                </div>
                <div class="column" v-show="data.site && data.site !='?'">
                  <span class=""  v-html="data.site"></span>
                </div>
              </div>   
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  import axios from 'axios'
  import Chess from 'chess.js'
  import Chessboard from 'chessboardjs'
  import snackbar from '../components/Snackbar'
  import swal from 'sweetalert'

  export default {
    name: 'game',
    watch: {
      '$route': function () {
        this.gameSeek()
      }
    },
    mounted: function(){
      if(localStorage.getItem('speed')){
        this.speed = parseInt(localStorage.getItem('speed'))
      }

      axios.get('/assets/json/eco_es.json')
        .then((response)=>{
        this.eco = response.data
        this.gameStart()
      })
    },
    methods: {
      gameMove:function(){
        if(!this.paused){
          var move = this.gameMoves[this.index];
          this.selectedIndex = parseInt(location.hash.replace('#',''))

          // exit if the game is over
          if (!move || this.game.game_over() === true ||
            this.game.in_draw() === true ||
            this.gameMoves.length === 0) return;

          document.querySelectorAll(this.squareClass).forEach((item) => {
            item.classList.remove('highlight-move');
          })

          document.querySelectorAll('.moveindex').forEach((item) => {
            item.parentNode.classList.remove('active');
          })

          var perc = (this.index + 1) / this.gameMoves.length * 100;
          $('.bar-progress').animate({width:perc+'%'},this.speed,'linear')
          document.querySelector('.moveindex[href="#' + this.index + '"]').parentNode.classList.add('active')

          var n = document.querySelector('.moveindex[href="#' + this.index + '"]').parentNode.offsetTop
          var x = document.querySelector('.moveindex[href="#' + this.index + '"]').parentNode.clientHeight
          var y = n + x
          var h = parseInt(document.querySelector('.movesTableContainer').style.height)
          if(y>h){
            document.querySelector('.movesTableContainer').scrollTop = n
          }

          this.index++
          const m = this.game.move(move)
          var pgn = []

          this.game.history().forEach((move,i) => {
            if(i%2){
              pgn.push(move)
            } else {
              pgn.push([Math.ceil(i/2)+1,move].join(' '))     
            }   
          })

          const pgnt = pgn.join(' ')
          this.eco.forEach((eco,i) => {
            if(eco.pgn === pgnt){
              this.opening = eco.name
              this.ecode = eco.eco
            }
          })

          this.board.position(this.game.fen());  

          if(m){
            document.querySelector('.square-' + m.from).classList.add('highlight-move')
            document.querySelector('.square-' + m.to).classList.add('highlight-move')
          }


          if(this.index === this.gameMoves.length){
            this.gamePause()
          }

          setTimeout(this.gameMove, this.speed)
        }
      },
      gamePGN:function(pgn){
        var data = []
        pgn.split('.').forEach(function(turn){
          turn.split(' ').forEach(function(move){
            if(move.length){
              if(isNaN(move) && move.length > 1){
                data.push(move)
              }
            }
          })
        })
        return data
      },
      gamePGNIndex:function(pgn){
        var data = []
        , index = 0
        , selectedIndex = parseInt(location.hash.replace('#',''))
        , symbols = [
          {K:'',Q:'',B:'',N:'',R:'',p:''},
          {K:'',Q:'',B:'',N:'',R:'',p:''}
        ]
        pgn.split('.').forEach(function(turn,i){
          const white = turn.split(' ')[1]||''
          const black = turn.split(' ')[2]||''
          if(isNaN(white)){
            data.push({
              white:white,
              black:black,
              i:Math.ceil(i*2),
              odd:i%2==0
            })
          }
        })
        return data
      },  
      gameStart: function(){
        this.$root.loading = true
        axios.post( this.$root.endpoint + '/game', {room:this.$route.params.game} ).then((res) => {
          if(!Object.keys(res.data).length) return location.href="/404"
          var game = res.data
          const totalms = this.$root.countMoves(game.pgn) * this.speed
          this.gameMoves = this.gamePGN(game.pgn)
          this.pgnIndex = this.gamePGNIndex(game.pgn)
          this.data = game
          this.duration = totalms / 1000
          this.$root.loading = false

          /*
          setTimeout(() => {
            axios.post( this.$root.endpoint + '/playlist', {white:game.white,black:game.black} ).then((res) => {
              console.log("playlist")
              console.log(res.data)
            })            
          },5000)*/

          setTimeout(() => {
            this.boardEl = document.getElementById('board')
            this.game = new Chess()
            this.board = Chessboard('board', this.boardCfg)      
            
            document.querySelector('.pres-container .is-player-white').classList.add('slideOutTL') 
            document.querySelector('.pres-container .is-player-black').classList.add('slideOutTR') 
            document.querySelector('.pres-container .is-player-info').classList.add('slideOutB') 
            document.querySelector('.pres-container').classList.add('fadeOut')   
            setTimeout(() => {
              document.querySelector('.pres-container').style.display = 'none'
            },1500)           

            const offset = 100
            setTimeout(() => {
              document.querySelector('.board').classList.add('show')

              document.querySelector('.movesTableContainer').style.height = ($('.board').height() - offset) + 'px'

              setTimeout(() => {
                /* autoplay kickstart */
                this.gameSeek()
              }, 500)
            }, 500)
          },2000)
        })
      },
      gameFlip: function(){
        this.board.flip()
        const head = document.querySelector('.board > .head').innerHTML
        const foot = document.querySelector('.board > .foot').innerHTML
        document.querySelector('.board > .head').innerHTML = foot
        document.querySelector('.board > .foot').innerHTML = head
      },
      gameSeek:function(){
        window.setTimeout(() => {
          this.selectedIndex = parseInt(location.hash.replace('#',''))
          if(!isNaN(this.selectedIndex)) {
            this.gamePos(this.selectedIndex)
          }
          this.gameMove()
          if(!isNaN(this.selectedIndex) && !this.paused) {
            this.gamePause()
          }
        }, 10)
      },
      gamePos:function(pos){
        if(pos > this.gameMoves.length){
          return
        }

        this.index = pos

        const moves = this.gameMoves.slice(0,this.index)
        var move = this.gameMoves[this.index];
        // ---------------
        var pgn = []
        moves.forEach((move,i) => {
          if(i%2){
            pgn.push(move)
          } else {
            pgn.push([Math.ceil(i/2)+1,move].join('. '))     
          }   
        })

        document.querySelectorAll('.square').forEach((item) => {
          item.classList.remove('highlight-move')
        })

        document.querySelectorAll('.moveindex').forEach((item) => {
          item.parentNode.classList.remove('active');
        })

        document.querySelector('.moveindex[href="#' + this.index + '"]').parentNode.classList.add('active');

        var perc = (this.index + 1) / this.gameMoves.length * 100;
        $('.bar-progress').animate({width:perc+'%'},100,'linear')
        const pgns = pgn.join(' ')
        this.game.reset()

        this.game.load_pgn(pgns) 
        this.board.position(this.game.fen())
      },
      gamePause:function(){
        this.paused = !this.paused
        document.querySelector('.bar-progress').classList.remove('paused')
        if(this.paused){
          document.querySelector('.bar-progress').classList.add('paused')
        } else {
          setTimeout(this.gameMove, 500)
        }
      },
      gameSpeed:function(s){
        this.speed+= s
        if(this.speed >= 1000 && this.speed <= 10000){
          localStorage.setItem('speed',speed)
        } else {
          this.speed-=s
        }
      },
      onMoveEnd: function() {
        document.querySelectorAll('.square-' + this.squareToHighlight).forEach((square) => {
          square.classList.add('highlight-move');
        })
      },
      setClock : function(){
        this.gamePause()
        swal("Ingresa el intervalo en milisegundos entre 1000/60000", {
          content: {
            element: 'input',
            attributes: {
              placeholder: "Valor en milisegundos",
              value: this.speed
            }
          },
          closeOnClickOutside:false
        })
        .then((speed) => {
          if(speed){
            speed = parseInt(speed)
            if(speed > 60000 || speed < 1000){
              swal('El valor debe ser entre 1000 / 60000')
            } else {
              this.speed = speed
              localStorage.setItem('speed',speed)
              const totalms = this.$root.countMoves(this.data.pgn) * this.speed
              this.duration = totalms / 1000
            }          
          }
          this.gamePause()              
        })
        .catch(() => {
          this.gamePause()
        });        
      }
    },
    data () {
      return {
        boardCfg: {
          showErrors:true,
          position: 'start',
          draggable: false,
          onMoveEnd: this.onMoveEnd,
          moveSpeed:250,
          pieceTheme:'/assets/img/chesspieces/wikipedia/{piece}.png'
        },
        data:{},
        eco:{},
        duration:0,
        ecode:'&nbsp;',
        opening:'&nbsp;',
        board:null,
        game:null,
        gameMoves:[],
        pgnIndex:[],
        room: location.pathname.replace('/',''),
        selectedIndex: parseInt(location.hash.replace('#','')),
        boardEl:document.getElementById('board'),
        index:0,
        paused:false,
        speed:3000,
        squareToHighlight:null,
        squareClass:'.square-55d63'
      }
    }
  }
</script>

<style type="text/css">
  .game-container {
    min-height: 100%;
  }
  .datospartida {
    margin-top: 0.75rem;
    margin-right: 1rem;
  }
  .highlight-move {
    filter: saturate(400%);
  }
  .is-pgn {
    line-height: 2rem;
    font-size: 0.75rem;
  }
  .moveCell.active {
    animation:.25s ease 0s normal forwards 1 contrastIn;
    -webkit-animation:.25s ease 0s normal forwards 1 contrastIn;
    background-color:#23d160;
    border-color: transparent;
    color: white;
  }
  .moveCell.active a {
    background-color:#23d160;
    color: white;
  }
  .status {
    display: inline-block;
    background-color: #fff;
    padding: 0.25rem;
    width: 100%;
    position: fixed;  
    z-index: 10;
    bottom: 0;
  }
  .pres-container {
    display: inline-block;
    background: rgb(255,255,25);
  background: -webkit-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
  background: -o-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
  background: linear-gradient(to right, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 199;
  }
  .pres > .columns > .column{
    height: 50vh;
  }
  .pres .is-result .column {
    padding-top: 5rem;
  }
  .pres h1 {
    font-size: 5rem;
    min-height: 6rem;
  }
  .board {
    opacity: 0;
    max-height:0;
  }
  .board.show {
    height:auto;
    max-height:600px;     
    opacity: 1;
  }
  .board h6 {
    margin-bottom: 0;
    padding: 0.75rem;
    line-height: 0.75rem;
  }
  .bar {
    position: relative;
    z-index: 99;
    background-color: rgba(0,0,0,0.1);
    border-radius: 5px;
    cursor: pointer;
  }
  .bar-progress {
    height: 0.5rem;
    background-color: red;
    border-radius: 5px;
    width: 0;
  }  
  .bar-progress.paused {
    background-color: grey;
  }  
  .is-player-info h2, .is-player-info h6 {
    color: #707070;
  }
  .pietimer {
    position: relative;
    margin: 40px auto;
    background: white;
  }
  .pietimer, .pietimer * {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }

  .pietimer {
    width: 250px;
    height: 250px;
  }

  .pietimer .pie {
    width: 50%;
    height: 100%;
    transform-origin: 100% 50%;
    position: absolute;
    background: #08C;
    border: 5px solid rgba(0,0,0,0.5);
  }

  .pietimer .spinner {
    border-radius: 100% 0 0 100% / 50% 0 0 50%;
    z-index: 200;
    border-right: none;
    animation: rota 5s linear infinite;
  }

  .pietimer:hover .spinner,
  .pietimer:hover .filler,
  .pietimer:hover .mask {
    animation-play-state: running;
  }

  .pietimer .filler {
    border-radius: 0 100% 100% 0 / 0 50% 50% 0;
    left: 50%;
    opacity: 0;
    z-index: 100;
    animation: opa 5s steps(1, end) infinite reverse;
    border-left: none;
  }

  .pietimer .mask {
    width: 50%;
    height: 100%;
    position: absolute;
    background: inherit;
    opacity: 1;
    z-index: 300;
    animation: opa 5s steps(1, end) infinite;
  }

  @keyframes rota {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes opa {
    0% {
      opacity: 1;
    }
    50%, 100% {
      opacity: 0;
    }
  }  
</style>


