<template>
  <div>
    
    <svg v-show="$root.loading" class="spinner-container" viewBox="0 0 44 44">
        <circle class="path" cx="22" cy="22" r="20" fill="none" stroke-width="4"></circle>
    </svg>
  
    <div class="menu hidden-loading slideDown">
      <div class="menu-container is-flex columns is-vcentered">
        <div class="has-text-left">
          <router-link class="menu-logo" to="/">
            <img src="/assets/img/logo.png" alt="AjedrezEV">
          </router-link>
        </div>

        <div class="column menu-primary">
          <div class="menu-bg"></div>
          <div class="menu-burger">
            <svg viewBox="0 0 800 600">
              <path d="M300,220 C300,220 520,220 540,220 C740,220 640,540 520,420 C440,340 300,200 300,200" id="top"></path>
              <path d="M300,320 L540,320" id="middle"></path>
              <path d="M300,210 C300,210 520,210 540,210 C740,210 640,530 520,410 C440,330 300,190 300,190" id="bottom" transform="translate(480, 320) scale(1, -1) translate(-480, -318)"></path>
            </svg>
          </div>
        </div>       
      </div>

      <div class="menu-items">
        <router-link to="/"><img src="/assets/img/logo.png"></router-link>
        <div class="menu-links has-text-left">
          <router-link to="/live">
            <span class="icon">
              <span class="fas fa-fire"></span> 
            </span>
            <span>En vivo</span>
          </router-link>
          <router-link to="/results">
            <span class="icon">
              <span class="fas fa-list"></span>
            </span> 
            <span>Resultados</span>
          </router-link>
          <!--router-link to="/puzzles"><span class="aev-icon"></span> Puzzles</router-link-->
          <router-link to="/about">
            <span class="icon">
              <span class="fa fa-info"></span>
            </span> 
            <span>Acerca de</span>
          </router-link>
          <router-link to="/contact">
            <span class="icon">
              <span class="fas fa-envelope"></span>
            </span> 
            <span>Contacto</span>
          </router-link>
          <hr>
          <div class="has-text-centered">
            <router-link class="button is-large is-white is-outlined" to="/stockfish">
              <span class="icon">
                <span class="fas fa-chess-knight"></span>
              </span> 
              <span>Jugar</span>
            </router-link>
          </div>
        </div>
      </div>    
    </div>
    
    <keep-alive v-show="!$root.loading" exclude="game,stockfish,results,live">
      <router-view/>
    </keep-alive>

    <div class="tosprompt"></div> 

    <div class="app__snackbar"></div>

  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      ver: '2.0.1'
    }
  }
}
</script>



